var timer = true;
var homePage = document.querySelector('.home-page')

function onScroll(e) {
    if (window.innerWidth > 940) {   
        if (e.preventDefault) { e.preventDefault(); } else { e.returnValue = false; }
        if (timer) scrollTo(homePage, homePage.scrollLeft + (e.deltaY * 5), 1000)
    } else {
        timer = true
    }
}


function scrollTo(element, to, duration) {
    timer = false

    var start = element.scrollLeft,
        change = to - start,
        currentTime = 0,
        increment = 20;
    var animateScroll = function () {
        currentTime += increment;
        var val = Math.easeInOutQuad(currentTime, start, change, duration);
        element.scrollLeft = val;
        if (currentTime < duration) {
            setTimeout(animateScroll, increment);
        } else {
            timer = true
        }
    };
    animateScroll();
}

//t = current time
//b = start value
//c = change in value
//d = duration
Math.easeInOutQuad = function (t, b, c, d) {
    t /= d / 2;
    if (t < 1) return c / 2 * t * t + b;
    t--;
    return -c / 2 * (t * (t - 2) - 1) + b;
};

homePage.addEventListener("mousewheel", onScroll, false); // Chrome
// face.addEventListener("DOMMouseScroll", scrollDoc, false); // FF

var menuBtn = document.querySelector('.header__menu'),
    menuBlock = document.querySelector('.nav'),
    menuCross = document.querySelector('.nav__cross')

menuBtn.addEventListener('click', function(e) {
    if (!menuBlock.classList.contains('active')) {
        menuBlock.classList.add('active')
    } else {
        menuBlock.classList.remove('active')
    }
})

menuCross.addEventListener('click', function(e) {
    menuBlock.classList.remove('active')
})